import { motion } from 'framer-motion';
import Image from 'next/image';
import { useRouter } from 'next/router';

export default function BookCard({ book, index }) {
  const { locale } = useRouter();
  const currentLocale = locale || 'ko';

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.8, delay: index * 0.2 }}
      className="group flex flex-col md:flex-row gap-8 md:gap-12 bg-white rounded-organic overflow-hidden p-6 md:p-10 shadow-soft hover:shadow-xl transition-all duration-500"
    >
      {/* Book Cover Image */}
      <div className="relative w-full md:w-[300px] aspect-[2/3] shrink-0">
        <motion.div
          whileHover={{ rotateY: 5, perspective: 1000 }}
          className="w-full h-full relative"
        >
          <Image
            src={book.coverImage}
            alt={book.title[currentLocale]}
            fill
            className="object-cover rounded-md shadow-lg"
            priority={index < 2}
          />
        </motion.div>
        
        {/* Awards Badge */}
        {book.awards && book.awards.length > 0 && (
          <div className="absolute -top-4 -left-4 bg-accent-terracotta text-white px-4 py-2 rounded-full text-xs font-bold shadow-md transform -rotate-12">
            {book.awards[0][currentLocale]}
          </div>
        )}
      </div>

      {/* Book Info */}
      <div className="flex flex-col justify-center">
        <div className="mb-6">
          <span className="text-xs uppercase tracking-[0.3em] text-accent-sage font-bold block mb-2">
            {book.publisher ? book.publisher[currentLocale] : 'Maya Moon'} Β· {book.publishDate}
          </span>
          <h3 className="text-3xl md:text-4xl font-display text-text-warm mb-3 group-hover:text-accent-rose transition-colors">
            {book.title[currentLocale]}
          </h3>
          <p className="text-lg font-serif italic text-text-warm/60">
            {book.subtitle[currentLocale]}
          </p>
        </div>

        <div className="space-y-4 mb-8">
          <p className="text-base text-text-warm/80 leading-organic line-clamp-4">
            {book.description[currentLocale]}
          </p>
        </div>

        {/* Reviews Quote */}
        {book.reviews && book.reviews.length > 0 && (
          <div className="bg-background-linen/50 border-l-4 border-accent-sage p-4 mb-8">
            <p className="text-sm italic text-text-warm/70">
              "{book.reviews[0].text}"
            </p>
            <span className="text-[10px] uppercase tracking-widest font-bold mt-2 block">β€” {book.reviews[0].source}</span>
          </div>
        )}

        {/* Upcoming Translations */}
        {book.translations?.upcoming && (
          <div className="flex items-center gap-2 mb-8">
            <span className="text-[10px] uppercase tracking-widest font-bold text-text-warm/40">Upcoming:</span>
            {book.translations.upcoming.map(lang => (
              <span key={lang} className="px-2 py-0.5 bg-background-beige text-text-warm/60 text-[10px] rounded-full">
                {lang}
              </span>
            ))}
          </div>
        )}

        {/* Purchase Links */}
        <div className="flex flex-wrap gap-4 mt-auto">
          {book.purchaseLinks.map((link) => (
            <a
              key={link.name}
              href={link.url}
              target="_blank"
              rel="noopener noreferrer"
              className="px-6 py-2 bg-accent-sage text-white rounded-full text-sm font-bold shadow-soft hover:bg-accent-terracotta transition-colors"
            >
              {link.name}
            </a>
          ))}
        </div>
      </div>
    </motion.div>
  );
}
