import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import { useTranslation } from 'next-i18next';
import Head from 'next/head';
import Layout from '@/components/Layout/Layout';
import BookCard from '@/components/Books/BookCard';
import { booksData } from '@/data/books';
import { motion } from 'framer-motion';

export default function Books() {
  const { t } = useTranslation('common');

  return (
    <Layout>
      <Head>
        <title>{`Books by Maya | Maya Flor`}</title>
        <meta name="description" content="Published books by Moon Hyejeong (Maya)" />
      </Head>

      <section className="section-container pt-[120px]">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="text-center mb-20"
        >
          <span className="text-xs uppercase tracking-[0.4em] text-accent-sage font-bold mb-4 block">
            {t('books_title')}
          </span>
          <h1 className="text-5xl md:text-6xl text-text-warm">{t('books_subtitle')}</h1>
          <div className="w-16 h-[1px] bg-accent-terracotta mx-auto mt-8" />
        </motion.div>

        <div className="flex flex-col gap-20">
          {booksData.map((book, index) => (
            <BookCard key={book.id} book={book} index={index} />
          ))}
        </div>
      </section>
    </Layout>
  );
}

export async function getStaticProps({ locale }) {
  return {
    props: {
      ...(await serverSideTranslations(locale, ['common'])),
    },
  };
}
